___CSS_LOADER_EXPORT___.push([module.id, `.admin-container {
  max-width: 1200px;
  margin: 2rem auto;
  padding: 1rem;
}

.admin-container h2 {
  text-align: center;
  margin-bottom: 2rem;
}

.refresh-button {
  background: #2196F3;
  color: white;
  border: none;
  border-radius: 6px;
  padding: 10px 20px;
  margin-bottom: 1rem;
  cursor: pointer;
  font-weight: bold;
}

.refresh-button:disabled {
  background: #90caf9;
  cursor: not-allowed;
}

.loading {
  text-align: center;
  padding: 2rem;
  color: #666;
}

.no-data {
  text-align: center;
  padding: 2rem;
  color: #666;
  background: #f5f5f5;
  border-radius: 6px;
}

.table-container {
  overflow-x: auto;
}

.members-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 1rem;
}

.members-table th, 
.members-table td {
  padding: 12px 15px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

.members-table th {
  background-color: #f2f2f2;
  font-weight: bold;
}

.members-table tr:hover {
  background-color: #f5f5f5;
}

.paid-row {
  background-color: #e8f5e9;
}

.pay-button {
  background: #4CAF50;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 8px 12px;
  cursor: pointer;
}

.paid-label {
  color: #2e7d32;
  font-size: 0.9rem;
}

.success-message {
  background-color: #e8f5e9;
  color: #2e7d32;
  padding: 1rem;
  margin-bottom: 1rem;
  border-radius: 6px;
  text-align: center;
}

.error-message {
  background-color: #ffebee;
  color: #d32f2f;
  padding: 1rem;
  margin-bottom: 1rem;
  border-radius: 6px;
  text-align: center;
}
